import { observer } from 'mobx-react-lite';
import { SyntheticEvent, useState } from 'react';
import { Reveal, Button } from 'semantic-ui-react';
import { Profile } from '../../app/models/profile';
import { useStore } from '../../app/stores/store';

interface Props {
  profile: Profile;
}

export default observer(function FollowButton({profile}: Props) {
  const {profileStore, userStore} = useStore();
  const {updateFollowing} = profileStore;
  const [loading, setLoading] = useState(false);

  function handleFollow(e: SyntheticEvent, username: string) {
    e.preventDefault();
    setLoading(true);
    updateFollowing(username, !profile.following)
      .finally(() => setLoading(false));
  }

  if (userStore.user?.username === profile.username) return null;

  return (
    <Reveal animated="move">
      <Reveal.Content visible style={{width: '100%'}}>
        <Button fluid color="teal" content={profile.following ? 'Following' : 'Not following'} />
      </Reveal.Content>

      <Reveal.Content hidden style={{width: '100%'}}>
        <Button fluid basic color={profile.following ? 'red' : 'green'} content={profile.following ? 'Unfollow' : 'Follow'}
                loading={loading} onClick={(e) => handleFollow(e, profile.username)} />
      </Reveal.Content>
    </Reveal>
  );
});
